import React, { Component } from 'react';
import Team from './team';


class About extends Component {
    render() {
        return (
<section id="about" className="about">
  <div className="container">
    <div className="section-title">
      <h2>Qui Sommes-nous?</h2>
      <p>Des professionnels en gestion des RH.</p>
    </div>
    <div className="row">
      <div className="col-lg-6 order-1 order-lg-2">
        <img src="/assets/img/human.jpg" width={500} height={150} className="img-fluid" alt />
      </div>
      <div className="col-lg-6 pt-4 pt-lg-0 order-2 order-lg-1 content">
        <h3>STRIDES HUMAN CAPITAL</h3>
        <p className="fst-italic">
          Crée à l’initiative de trois associés disposant d’une longue expérience dans le
          management et la direction d’entreprises, l’expertise RH et l’accompagnement à la
          transformation digitale, Strides Human Capital se veut un cabinet dont la vocation est de
          répondre aux nouvelles problématiques RH dans un environnement en grande mutation
          digitale.
        </p>
        <ul>
          <li><i className="bi bi-check-circled" /> La principale problématique RH à laquelle font face de nos jours les entreprises en
            phase de transformation digitale ainsi que les startups, concerne la faible disponibilité de
            compétences et la difficulté à les fidéliser. Face à une volonté de départ, les entreprises sont
            quasi systématiquement dans une position de réaction, avec une perte considérable de
            temps dans le développement ou la transformation digitale.</li>
        </ul>
        <p>
          Nous nous positionnons, au travers de notre concept « Assurance RH » dans une
          approche permettant d’y apporter une solution.
        </p>
      </div>
    </div>
    <section id="featured-services" className="featured-services section-bg">
      <div className="container">
        <div className="row no-gutters">
          <div className="col-lg-4 col-md-6">
            <div className="icon-box">
              <div className="icon"><i className="bi bi-laptop" /></div>
              <h4 className="title"><a href>ETHIQUE</a></h4>
              <p className="description">Garantir la confidentialité des données, ainsi que la transparence de nos processus vis-à-vis des entreprises et des candidats.</p>
              <p>Respecter les candidats et les entreprises : attention particulière portée aux personnalités et à la culture de chaque entreprise.</p>
            </div>
          </div>
          <div className="col-lg-4 col-md-6">
            <div className="icon-box">
              <div className="icon"><i className="bi bi-briefcase" /></div>
              <h4 className="title"><a href>EXPERTISE / QUALITÉ / EFFICIENCE</a></h4>
              <p className="description">Nous mettons à la disposition de nos clients diverses formations avec des formateurs
reconnus, contribuant ainsi au perfectionnement et à l’actualisation des compétences,
mais aussi à la fidélisation des compétences et talents indispensables à l’entreprise et
à la startup.</p>
            </div>
          </div>
          <div className="col-lg-4 col-md-6">
            <div className="icon-box">
              <div className="icon"><i className="bi bi-calendar4-week" /></div>
              <h4 className="title"><a href>FLEXIBILITÉ / CAPACITÉ D'ADAPTATION</a></h4>
              <p className="description">STRIDES HUMAN CAPITAL est capable de s’adapter aux différents contextes, tout en garantissant des services et prestations personnalisés en fonction de vos besoins.</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</section>

          
            
)
    }
}
export default About;